import React, { useState, useContext } from "react";
import { useHistory } from "react-router-dom";
import {AppContext} from '../../store/Store';
import Card from '../Card/Card';


const RandomTen = () => {
  const history = useHistory();
  const {words} = useContext(AppContext);

    const listOfCards = words.map(item => (
        <Card key={item.plValue} PL_WORD={item.plValue} ENG_WORD={item.engValue}/>
    ))
    
  return (
    <div className="RandomTen">
      <p className="AddWords__back" onClick={() => history.goBack()}>
        Wróć
      </p>

        {words.length && listOfCards}
      
    </div>
  );
};

export default RandomTen;
